import React,{useState} from "react";



let App=()=>{
const[state,setRegist]=useState({
    username:"",
    password:"",
    email:"",
    registration:""
})
 const [records, setrecords] = useState();
    let eventhandling=(e)=>{

        let name=e.target.name;
        let value=e.target.value;
        setRegist({...state, [name] : value});
    console.log(name+value);
    }

const handleSubmit=(e)=>{
    e.preventDefault();
    const newrecords={...state};
    setrecords([...records,newrecords]);setRegist({username:"",password:"",email:"",registration:""})
}
    return(<>
<form onSubmit={handleSubmit}>
    <label htmlFor="username" >Name</label>
    <input type="text" value={state.username} onChange={eventhandling} name="username"/><br></br>
    <label htmlFor="password" >Password</label>
    <input type="text" value={state.password} onChange={eventhandling} name="password"/><br></br>
    <label htmlFor="email" >Email</label>
    <input type="text" value={state.email} onChange={eventhandling} name="email"/><br></br>
    <label htmlFor="registration" >Registration number</label>
    <input type="text" value={state.registration} onChange={eventhandling} name="registration"/><br></br>
    <button type="submit">submit</button>

   

</form>
<div>{
    records.map((curElem)=>{return(<>
        <p>{curElem.username}</p>
        <p>{curElem.password}</p>
        <p>{curElem.email}</p>
        <p>{curElem.registration}</p></>
     ); })}
</div></>);
}
export default App;